@import "Common.scss";
nav.tab_bar>ul.List>li>a{color: #c0c1c2;}
html,body{width: 100%; height: 100%;}
.tab_bar>ul.List>li:nth-child(2)>a{color: #513736;}
main{height: 85.8%; padding-top: 64px;}

/* search_box */
.search_box{display: flex; flex-direction: row; justify-content: center;
    align-items: center; height: 7.3%; min-height: 53px;}
.search_inner{display: flex; flex-direction: row;
    justify-content: space-between; width: 88%; height: 50%;
    background-color: #F1F2F4; border-radius: 5px;}
.search_inner>i{font-size: 16px; color: #BEBCC1; align-self: center; 
    margin-left: 15px;}
.search_inner>input{width: 90%; background: transparent; border: none;
    padding: 0 15px;}
.search_inner>input::placeholder{text-align: center;}

/* main_section */
.main_section>ul{display: flex; flex-direction: column;
    border-top: 1px solid #ebebeb;}
.main_section>ul>li{border-bottom: 1px solid #ebebeb;}
.main_section>ul>li>a{display: flex; flex-wrap: wrap;
    font-size: 16px; align-items: center;}
.main_section .chats_img{width: 50px; height: 50px; margin:15px;}
.main_section>ul>li>a>span.chats_img>img{width: 50px; height: 50px;
    border-radius: 50%; background-image: cover;}

.main_section .chats_cont{flex: auto; display: flex; flex-direction: column;
    justify-content: center;}
.main_section .chats_cont .chats_latest{font-size: 12px; color: #8e9399;}
.main_section .chats_time{margin-right: 15px;
    font-size: 12px; color: #8e9399;}


/* floating action btn */
.chat_fa_btn{position: fixed; right: 20px; bottom: 80px;}
.chat_fa_btn>a{display: flex; justify-content: center; align-items: center;
    width: 60px; height: 60px; background-color: #FFE84C; border-radius: 50%;
    box-shadow: 0 2px 6px #0004; font-size: 18px; color: #513736;
    transition: color 300ms linear 0s, background 300ms linear 0s;}
.chat_fa_btn>a:hover{background: #513736; color: #FFE84C;}
