@import "Common.scss";
.body{width: 100%; height: 100%;}

.Chatting_main{display: flex; flex-direction: column;
    height: 900px; width: 100%; padding-top: 64px; background: #A1C0D5;}
.date_info{opacity: 0.5; text-align: center; margin: 10px;}


/* header */
.chattingheader{color: #000; background-color: #A1C0D5;
    border-bottom: 1px solid #96ACBA;}
.title_bar>.left_item>a{color: #000; opacity: 1; font-size: 24px;}
.title_bar>.right_item>a{color: #000; opacity: 1; font-size:16px;}
.title_bar>.right_item>a>i{padding-left: 5px;}

/* 공통 */
.chat_box{display: flex; flex-direction: column;
    align-items: flex-start;
    width: fit-content; max-width: calc(100% - 80px);
    position: relative;}
.chat{font-size: 16px; line-height: 1; margin: 5px;
    padding: 10px 5px;
    border-radius: 5px; background: #fff;}

/* my */
.my{align-self: flex-end; align-items: flex-end; max-width: 240px; margin-right: 10px;}
.my .chat{background: #FFE73A;}
.my .chat_time{position: absolute; left: -30px; bottom: -5px;
    width: 50px; opacity: 0.5;}

/* other */
.other{margin-left: 70px; margin-top: 20px;}
.profile_img{position: absolute; left: -70px; top: -5px;
    width: 35px; height: 35px;}
.profile_name{display: block; font-weight: bold; margin: 5px;}
.other .chat_time{position: absolute; right: -30px; bottom: -5px; width: 50px; opacity: 0.5;}

/* footer */
.footer{display: flex; justify-content: space-between; align-items: center;
    position: fixed; left: 0; bottom: 0; width: 100%; height: 45px; background: #eee;}
/* .plus_btn{z-index: 3; position: relative; bottom: -25px;} */
.plus_btn a{color: #B3B4B4; padding-left: 15px; font-size: 16px;}
.footer>form{width: calc(100% - 40px); display: flex; justify-content: flex-end;}
fieldset.text_box{position: relative; display: flex; justify-content: flex-end; align-items: center; width: 100%;}
.text_field{width: calc(100% - 40px); height: 32px;}
.emoticon_btn{position: absolute; right: 45px;}
.emoticon_btn a{color: #B3B4B4;}
.voice_btn a{display: block; width: 34px; height: 37px;
margin-right: 5px; line-height: 37px;
text-align: center; background: #FFE73A; color: #513736; font-size: 16px;}
